:root {
  --primary: #e0ff02;
  --secondary: #3d3b47;
  --background: #111111;
}

.socialicon{
    display: flex;
    background-color: var(--secondary);
    border-radius: 30px;
    color: var(--primary);
    width: 80%;
    margin: 10px auto;
}

.socialicon img{
    width: 35px;
    height: 35px;
    margin: auto 20px;
}

.contactssection{
    text-align: center;
    margin: 0 auto;
    padding-bottom: 80px;
}

.emailtext{
    font-size: 12pt;
    padding: 5px 0 5px 0;
}